@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

body,
html {
  overflow-x: hidden !important;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* .bs1 {
  box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
} */

.ant-row {
  margin: 0 20 !important;
}

.spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 9999;
}

h1 {
  font-size: 30px;
  font-weight: bold;
  color: orangered;
}

p {
  margin: 5px !important;

  padding: 0;
}

.btn1 {
  box-shadow: none !important;
  background-color: rgb(255, 254, 254) !important;
  color: rgb(0, 0, 0) !important;
  border: 1px solid rgb(0, 0, 0) !important;
  padding: 5px 15px !important;
  outline: none !important;
  border-radius: 4px !important;
}

.bt1:focus {
  border: none;
  
}

button {
  padding: 3px 10px !important;
}

.header {
  padding: 20px;
  padding-bottom: 8px;
  background-color: #08AEEA;
background-image: linear-gradient(150deg, #08AEEA 0%, #2AF598 100%);
 
}

.header .ant-btn {
  /* box-shadow: none !important; */
  background-color: rgb(0, 0, 0) !important;
  color: rgb(255, 251, 251) !important;
  border: 1px solid rgb(79, 173, 76) !important;
  padding: 3px 15px !important;
  outline: none !important;
  margin-top: 5px;
}

.content {
  min-height: 90vh;
  background-color: #FEE140;
  background-image: linear-gradient(315deg, #FEE140 0%, #FA709A 100%);
  margin-top: 0px;

  
}

.footer {
  background-color: rgb(0, 0, 0);
  padding: 10px;
  color: white;
}


/* this is just for ui purpose , no functionality is related */
.car {
  border-radius: 5px;
  margin-top: 100px;
  height: 160px;
  transition: 0.3s;
}

.car:hover {
  height: 220px;
  color:white;
  background-color: black;
}

.car:hover>.car-content {

  opacity: 1;
  -webkit-transform: translateY(-40px);
          transform: translateY(-40px)
}

.carimg {
  height: 180px;
  width: auto;
  border-radius: 5px;
  -webkit-transform: translateY(-50px);
          transform: translateY(-50px);
}

.car-content {
  -webkit-transform: translateY(-200px);
          transform: translateY(-200px);
  opacity: 0;
  transition: 0.3s;
}

.login {
  background-color: #4158D0;
background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);

  padding-top: 50px;
  min-height: 100vh;
}

.login label,
.login h1 {
  color: white !important;
  opacity: 0.6;
}

.login-form {
  background-color: black;

  opacity:0.7;
  
}

.login-form input {
  background-color: #333333 !important;
  border: none !important;
  color: white;
  opacity: 0.5;
}

.login-logo {
  position: absolute;
  /* color: black; */
  left: 0; 
  right: 0;
  top: 60%;
  bottom: 50%;
  -webkit-transform: translateY(-50%, -50%);
          transform: translateY(-50%, -50%);
  font-size: 45px;
  font-weight: bolder;
  
  /* opacity: 0.2 !important; */
  


}

.login a {
  color: white !important;
  opacity: 0.5;
}

.carimg2 {
  height: 400px;
  border-radius: 10px;
  width: auto;
}

svg {
  height: 20px !important;
  width: 20px !important;
}

a {
  color: rgb(0, 0, 0) !important;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(6, 6, 6);
}

.App-link {
  color: #fbfeff;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

